import 'pretendard/dist/web/static/pretendard.css';
import { createTheme } from '@mui/material/styles';
import { grey } from '@mui/material/colors';

// const theme = createTheme({
//     palette: {
//         primary: {
//             main: '#1A3C65',
//         },
export const getTheme = (mode: 'light' | 'dark') =>
    createTheme({
        palette: {
            mode,
            primary: {
                //main: '#1A3C65',
                main: '#323F53',
            },
        // secondary: { // secondary      .
        //   main: '#dc004e',
        // },
        // error: {
        //   main: '#f44336',
        // },
        // warning: {
        //   main: '#ff9800',
        // },
        // info: {
        //   main: '#2196f3',
        // },
        // success: {
        //   main: '#4caf50',
        // },
    },
    typography: {
        fontFamily: [
            'Pretendard',
            '-apple-system',
            'BlinkMacSystemFont',
            'system-ui',
            'Roboto',
            '"Helvetica Neue"',
            '"Apple SD Gothic Neo"',
            '"Noto Sans KR"',
            'sans-serif',
        ].join(','),
        h1: {
            fontSize: '2.25rem',
            fontWeight: 600,
            // ...  h1 
        },
        button: {
            textTransform: 'none',
        },
    },
    components: {
        MuiAccordion: {
            styleOverrides: {
                root: { // Accordion  
                    border: `1px solid ${mode === 'light' ? grey[300] : grey[700]}`,
                    '&:not(:first-of-type)': {
                        borderTop: 'none',
                    },
                    '&.Mui-expanded': { //  
                        margin: 0,
                    },
                    boxShadow: 'none',

                    //     .
                    '&:first-of-type': {
                        borderTopLeftRadius: 0,
                        borderTopRightRadius: 0,
                    },
                    //    .
                    '&:last-of-type': {
                        borderBottomLeftRadius: 0,
                        borderBottomRightRadius: 0,
                    },

                },
            },
        },
        MuiAppBar: {
            styleOverrides: {
                root: { // AppBar    
                    boxShadow: 'none', //   .
                    //  elevation={0}  MUI      .
                    // : boxShadow: theme.shadows[0] (theme   )
                },
            },
        },
    },
    spacing: 4,
});

// export default theme;

